body .invoice{
    align-content: center;
    align-items: center;
    border: none;
    bottom: 0;
    display: flex;
    flex-direction: column;
    height: 87%;
    overflow-x: hidden;
    overflow-y: auto;
    position: fixed;
    right: 0;
    width: 85%;
    z-index: 100;
}

.invoice::-webkit-scrollbar:vertical{
    background: #dbdbdb;
    display: flex;
    height: .6rem;
    width: .35rem;
}

.invoice::-webkit-scrollbar-thumb:vertical{
    background: #1aafef;
    border-radius: 10px;
}

/* Modal */
.ReactModalPortal > div{ opacity: 0; }

.ReactModalPortal .ReactModal__Overlay {
    align-items: center;
    display: flex;
    justify-content: center;
    overflow-y: scroll;
    transition: opacity .2s ease-in-out;
    z-index: 999;
}

.modal-fondo {
    background-color: rgba(0, 0, 0, 0.3);
    bottom: 0;
    left: 0;
    position: fixed;
    right: 0;
    top: 0;
}

.ReactModalPortal .ReactModal__Overlay--after-open { opacity: 1; }

.ReactModalPortal .ReactModal__Overlay--before-close { opacity: 0; }

.modal {
    background: #f7f7f7;
    border-radius: .7rem;
    color: rgb(51, 51, 51);
    display: inline;
    height: 500px;
    width: 400px;
    outline: none;
    padding: 10px 1.5rem;
}

/* Modal search client invoice */
.modal-search-client-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: 1rem;
    width: 100%;
}

.form-search-client-invoice {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.label-search-client-invoice {
    font-size: 1.2rem;
    font-weight: bold;
}

.input-search-client-invoice {
    background: #FFFFFF;
    border: 1px solid #cccccc;
    border-radius: .35rem;
    box-sizing: border-box;
    font-size: 1.1rem;
    height: 2.3rem;
    margin-top: .5rem;
    padding: 0 1rem;
    width: 100%;
}

.input-search-client-invoice:focus {
    border: 1px solid rgb(3, 139, 198);
    box-shadow: 0 0 5px #15749d;
    outline: none;
}

.input-search-client-invoice:hover { border: 1px solid rgb(3, 139, 198); }

.buttons-container-search-client-invoice {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    margin-top: .7rem;
}

.button-search-client-invoice, .button-cancel-client-invoice {
    background: none;
    border-radius: .4rem;
    font-size: 15px;
    font-weight: 600;
    letter-spacing: 0.05em;
    padding: .5rem 1rem;
    width: 46%;
}

.button-search-client-invoice {
    background-color: #0191CE;
    border: none;
    border-color: #0191CE;
    color: #fff;
}

.button-search-client-invoice:hover {
    background: #005d84;
    color: #FFFFFF;
    cursor: pointer;
}

.button-cancel-client-invoice {
    border: 2px solid #ef020a;
    border-color: #ef020a;
    color: #ef020a;
}

.button-cancel-client-invoice:hover {
    background: #ef020a;
    color: #FFFFFF;
    cursor: pointer;
}

.container-list-find-clients-invoice {
    background-color: #fff;
    border: .05rem solid #cccccc;
    border-radius: .5rem;
    display: flex;
    flex-direction: column;
    height: 21rem;
    margin-top: 1rem;
    overflow-y: scroll;
}

.container-list-find-clients-invoice::-webkit-scrollbar:vertical{
    background: #dbdbdb;
    display: flex;
    height: .6rem;
    width: .35rem;
}

.container-list-find-clients-invoice::-webkit-scrollbar-thumb:vertical{
    background: #1aafef;
    border-radius: 10px;
}

.item-list-find-clients-invoice {
    cursor: pointer;
    display: flex;
    flex-direction: column;
    margin-bottom: .5rem;
}

.item-info-client-invoice {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.title-item-find-client {
    font-size: 1.1rem;
    font-weight: bold;
    margin: .5rem 0;
    text-align: right;
    width: 25%;
}

.detail-item-find-client {
    font-size: 1.1rem;
    font-weight: 100;
    margin: .5rem 0;
    text-align: left;
    width: 70%;
}

.hr-clients-invoice {
    border: 1px solid #0191CE;
    border-radius: .2rem;
    height: 1px;
    margin: 0 1rem;
    padding: 0;
    width: 94%;
}
/* FIN: Modal search client invoice */

/* Modal actions products on invoice */
.btn-close-modal-x {
    background: none;
    border: none;
    color: #0191CE;
    font-size: 1.2rem;
    font-weight: bold;
    padding: 0;
    text-align: right;
    width: 100%;
}

.form-action-product-modal {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 0;
    width: 100%;
}

.form-action-product-modal header {
    align-items: center;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-top: .3rem;
}

.title-product-modal { margin: 0; }

.required-fields-product-modal {
    color: #0191CE;
    font-size: 15px;
    font-weight: 700;
}

.form-action-product-modal hr{ 
    background-color: #ccc;
    border: none; 
    height: 1px;
    margin-bottom: 1rem;
    width: 100%;
}

.form-action-product-modal i {
    color: #0191CE;
    font-size: 15px;
    font-weight: 800;
}

.form-action-product-modal input {
    background: #FFFFFF;
    border: 1px solid #D2D2D2;
    border-radius: .2rem;
    box-sizing: border-box;
    height: 2rem;
    margin-top: .5rem;
    padding: .5rem 1rem;
    width: 100%;
}

.form-action-product-modal label, .form-action-product-modal input, .form-action-product-modal p {
    font-size: .9rem;
    margin: 0;
    margin-bottom: .5rem;
}

.form-action-product-modal input:focus {
    border: 1px solid rgb(3, 139, 198);
    box-shadow: 0 0 5px #15749d;
    outline: none;
}

.form-action-product-modal input:hover { border: 1px solid rgb(3, 139, 198); }

.row-product-input-modal {
    display: flex;
    flex-direction: row;
}

.row-product-input-modal p { padding-left: .5rem; }

.row-product-input-modal label, .row-product-input-modal p { font-size: 1rem !important; }

.colum-product-input-modal {
    display: flex;
    flex-direction: column;
}

.buttons-actions-products-column {
    display: flex;
    justify-content: space-evenly;
    margin-top: .5rem;
}

.button-save-product-modal, .button-close-product-modal {
    background: none;
    border-radius: .4rem;
    font-size: 15px;
    font-weight: 600;
    letter-spacing: 0.05em;
    padding: .5rem 1rem;
}

.button-save-product-modal {
    border: 2px solid #0191CE;
    border-color: #0191CE;
    color: #0191CE;
}

.button-save-product-modal:hover {
    background: #0191CE;
    color: #FFFFFF;
    cursor: pointer;
}

.button-close-product-modal {
    border: 2px solid #ef020a;
    border-color: #ef020a;
    color: #ef020a;
}

.button-close-product-modal:hover {
    background: #ef020a;
    color: #FFFFFF;
    cursor: pointer;
}
/* FIN: Modal actions products on invoice */

.container-loading-invoice {
    align-items: center;
    display: flex;
    height: 100%;
    justify-content: center;
    width: 100%;
}

.container-loading-invoice .loading-invoice { color: #0191CE; }

input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button { 
    margin: 0; 
    -webkit-appearance: none; 
}

input[type=number] { -moz-appearance:textfield; }

::-webkit-scrollbar{
    background: #dbdbdb;
    display: flex;
    height: .35rem;
    width: 1rem;
}

::-webkit-scrollbar:vertical { display: none;}

::-webkit-scrollbar-thumb:horizontal {
    background: #1aafef;
    border-radius: 10px;
}

.invoice-client-card ::-webkit-scrollbar{ display: none; }

.actions-buttons-invoice {
