}

.action-button:last-child { margin-right: 0; }

.hidden-action-add, .hidden-action-edit, .hidden-action-reverse,
.hidden-action-delete, .hidden-action-save, .hidden-action-firmar { display: none; }

.btn-add-invoice, .btn-edit-invoice, .btn-reverse-invoice,
.btn-delete-invoice, .btn-save-invoice, .btn-firmar-invoice {
    align-items: center;
    background-color: #0191CE;
