.btn-reverse-invoice:hover + .hidden-action-reverse,
.btn-delete-invoice:hover + .hidden-action-delete,
.btn-save-invoice:hover + .hidden-action-save,
.btn-firmar-invoice:hover + .hidden-action-firmar { 
    display: block;
    text-align: center;
    font-weight: 700;
}

.icon-action-invoice {
    height: 25px;
    width: 25px;
}

.invoice-content {
