}

.react-datepicker__input-container input {
    background: #FFFFFF;
    border: 1px solid #D2D2D2;
    border-radius: .2rem;
    box-sizing: border-box;
    font-size: 1.2rem;
    height: 1.7rem;
    margin-top: .5rem;
