.invoice-num-input:focus, .react-datepicker__input-container input:focus {
    border: 1px solid rgb(3, 139, 198);
    box-shadow: 0 0 5px #15749d;
    outline: none;
}

.invoice-num-input:hover, .react-datepicker__input-container input:hover { border: 1px solid rgb(3, 139, 198); }

.fecha-invoice {
    align-items: center;
    display: flex;
    flex-direction: row;
    justify-content: center;
    height: 2rem;
}

.fecha-invoice-button {
    align-items: center;
