}

.tipo-invoice {
    display: flex;
    flex-direction: column;
}

.radio-tipo-invoice {
    align-items: center;
    display: flex;
    flex-direction: row;
    margin: .5rem 0 0 1rem;
}

.tipo-invoice-subtitle { font-size: 1rem; }

.space{ margin-left: 1rem; }

.radio-tipo-invoice-input {
    height: 1.3em;
    margin: 0;
    margin-left: .5rem;
    width: 1.3em; 
}

.radio-tipo-invoice-input:after {
    background-color: #d1d3d1;
    content: '';
}

.radio-tipo-invoice-input:checked:after {
    background-color: #0191CE;
    border: 2px solid white;
    border-radius: 15px;
    content: '';
    display: inline-block;
    height: 11px;
    left: 1px;
    position: relative;
    top: 1px;
    visibility: visible;
    width: 11px;
}

.invoice-select {
    display: flex;
    flex-direction: column;
}

.invoice-select-title {
    font-size: 1.1rem;
    font-weight: 700;
    margin-bottom: .5rem;
}

.invoice-selects-card select {
    appearance: none;
    background-color: transparent;
    border: none;
    cursor: inherit;
    font-family: inherit;
    font-size: 1.1rem;
    line-height: inherit;
    margin: 0;
    outline: none;
    padding: 0 1em 0 0;
    width: 100%;
    z-index: 1;
    -moz-appearance: none;
    -webkit-appearance: none;
}

.invoice-select select { height: 1.3rem; }

.invoice-select-input {
    align-items: center;
    background: #FFFFFF;
    border: 1px solid #D2D2D2;
    border-radius: .2rem;
    box-sizing: border-box;
    cursor: pointer;
    display: grid;
    grid-template-areas: "select";
    height: max-content;
    line-height: 1.1;
    padding: .2rem .5rem;
    position: relative;
}

.invoice-select-input:focus {
    border: 1px solid rgb(3, 139, 198);
    box-shadow: 0 0 5px #15749d;
    outline: none;
}

.invoice-select-input:hover { border: 1px solid #038bc6; }

.invoice-select-input::after {
    background-color: #0191CE;
    clip-path: polygon(100% 0%, 0 0%, 50% 100%);
    content: "";
    grid-area: select;
    height: 0.5em;
    justify-self: end;
    position: absolute;
    width: 0.8em;
}

